
@import './variables';
@import './libs/normalize';

@import './components/all';

body {
  background-color: $background;
}
::-webkit-scrollbar{
  width: 13px;
}
::-webkit-scrollbar-track {
  background: #F9F9F9;
  box-shadow:  0 0 2px rgba(0, 0, 0, .2);
}
::-webkit-scrollbar-thumb {
  background: #FE5F1E;
  border-radius: 8px;
  border:  2px solid #F9F9F9;
}

.wrapper {
  width: calc(100vw - 100px);
  height: 100%;
  background-color: #fff;
  margin: 50px auto;
  border-radius: 10px;
  max-width: 1400px;

  @media (max-width: 645px ) {
    width: calc(100vw - 30px);
    margin-top: 15px;
  }
}

.content {
  padding: 40px 0;

  &__title {
    font-weight: 800;
    font-size: 38px;
    margin-top: 60px;
    margin-bottom: 50px;

    @media(max-width: 640px) {
      font-size: 30px;
      margin-top: 25px;
      margin-bottom: 25px;
    }
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-column-gap: 11px;
   
    @media (max-width: 1400px ) {
      grid-template-columns: repeat(3, 1fr);
    }
  
    @media (max-width: 1060px ) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 730px ) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 1260px ) {
      ::-webkit-scrollbar{
        width: 15px;
      }
      ::-webkit-scrollbar-track {
        background: #F9F9F9;
        box-shadow:  0 0 2px rgba(0, 0, 0, .2);
      }
      ::-webkit-scrollbar-thumb {
        background: #FE5F1E;
        border-radius: 8px;
        border:  2px solid #F9F9F9;
      }

     .categories {
        width: 100%;
        overflow: auto;

       ul{
          width: 785px;
        }
      }

      .sort{
        padding-left: 20px;
        b{
          display: none;
        }
      }
    }

    @media (max-width: 760px) {
      .categories{
        ul {
          li {
             padding: 8px 20px;
          }
        }
      }
   }
   
    @media (max-width: 820px) {
      .categories{
        margin-top: 30px;
      }

      flex-direction: column-reverse;
    }

  }
}

.container {
  width: $container-width;
  margin: 0 auto;
  

  &--cart {
    max-width: 820px;
    
    margin: 90px auto;
     @media(max-width: 1030px) {
      margin-top: -40px;
     }
     @media(max-width: 645px) {
      padding: 20px;
     }
    .content__title {
      margin: 0;
    
     
    }
  }
}

.cart {
  
  width: 100%;
  &__top {
    
   
    display: flex;
    justify-content: space-between;
    align-items: center;
    
  }

  .content__title {
    display: flex;
    align-items: center;
    font-size: 32px;
    

    @media(max-width: 630px) {
      font-size: 17px;
    }
   
  

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;
      @media(max-width: 630px) {
        width: 20px;
        height: 20px;
        margin-right: 2px;
      }
      path {
        stroke: $black;
        stroke-width: 1.9;
      }
    }
  }

  &__clear {
    display: flex;
    align-items: center;
    cursor: pointer;
    @include noselect();



    span {
      display: inline-block;
      margin-left: 7px;
      color: #b6b6b6;
      font-size: 18px;

      @media(max-width: 630px) {
        font-size: 13px;
        margin-left: -1px;
      }
    }

    span,
    svg,
    path {
      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }
      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }
  }

 .content__items {
    display: block;
    
  }

  &__item {
    
    display: flex;
    justify-content: space-around;
    
    width: 100%;
    border-top: 1px solid $gray-line;
    padding-top: 30px;
    margin-top: 30px;

   

    &-img {
      display: flex;
      align-items: center;
      margin-right: 15px;
      width: 10%;
 
      

      @media(max-width: 630px) {
        margin-right: 25px;
        
      }
      @media(max-width: 530px) {
        margin-right: 35px;
      }
      @media(max-width: 430px) {
        margin-right: 40px;
      }

      img {
        width: 80px;
        height: 80px;
        @media(max-width: 630px) {
          width: 70px;
        height: 70px;
        }
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 40%;
      

      @media(max-width: 920px){
        margin: 5px;
        width: 130px;
       
      }
      h3 {
        font-weight: bold;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.01em;
        @media(max-width: 630px){
          font-size: 18px;
        }
      }

      p {
        font-size: 18px;
        color: #8d8d8d;
        @media(max-width: 630px){
          font-size: 14px;
        }
      }
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 13%;
      

      &-minus {
        
        svg {
         
          path:first-of-type {
            display: none;
            
            
          }
          
        }
        &:disabled {
          opacity: 0.4;
          border-color: #787878;

          &:active {
            transform: none;
          }
        
          &:hover {
            background-color: transparent;

            svg path {
              fill: #787878;
              
              
           }
          }

          svg path {
             fill: #787878;
          }
        }
      }

      b {
        font-size: 22px;
        @media(max-width: 630px){
          font-size: 18px;
          
         }
      }
    }

    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 33%;
      @media(max-width: 920px){
        width: 20%;
       
       }
    

      b {
        font-weight: bold;
        font-size: 22px;
        letter-spacing: 0.01em;
       
      }
    }

    &-remove {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 4%;
      
      @media(max-width: 630px) {
        margin-left: 20px;
        
      }

      .button {
        border-color: darken($color: $gray-line, $amount: 10);
       
        
      }

      svg {
        transform: rotate(45deg);
       @media(max-width: 630px){
        width: 10px;
        height: 10px;
       }

        path {
          fill: darken($color: $gray-line, $amount: 15);
        }
      }

      .button {
        svg {
          width: 11.5px;
          height: 11.5px;
          position: relative;
        }
        &:hover,
        &:active {
          border-color: darken($color: $gray-line, $amount: 80);
          background-color: darken($color: $gray-line, $amount: 80);
        }
      }
    }
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: flex;
      justify-content: space-between;

      span {
        font-size: 22px;
        @media(max-width: 600px){
          font-size: 16px;
        }

        &:last-of-type {
          b {
            color: $orange;
          }
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;

      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;
           
       @media(max-width: 630px) {
        width: 105px;
       height: 45px;
       
       
       }

        border-color: darken($color: $gray-line, $amount: 10);

        span {
          color: darken($color: $gray-line, $amount: 20);
          font-weight: 500;
          font-weight: 600;
          @media(max-width: 630px) {
          font-size: 14px;
           
           }
        }

        &:hover {
          background-color: darken($color: $gray-line, $amount: 90);
          border-color: darken($color: $gray-line, $amount: 90);

          span {
            color: $gray-line;
          }
        }

        svg {
          margin-right: 12px;
          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;
        @media(max-width: 630px) {
          width: 70px;
           padding: 5px;
           font-size: 15px; 
           font-weight: 400;
           height: 50px;
         }
      }
    }
  }

  &--empty {
    margin: 80px auto;
    width: 620px;
    text-align: center;
    
    @media(max-width: 740px){
      margin: 20px auto;
      width: 90%;
      padding: 20px;
   }

    h2 {
      font-size: 36px;
      font-weight: 700;
      margin-bottom: 15px;
      @media(max-width: 740px){
        font-size: 26px;
        margin: auto;
     }
    }

    p {
      font-size: 20px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
      @media(max-width: 740px){
        font-size: 18px;
        margin: 20px auto;
     }
    }

    icon {
      position: relative;
      top: 2px;
    }

    img {
      display: block;
      width: 300px;
      margin: 45px auto 60px;
      @media(max-width: 740px){
        width: 200px;
        margin: 40px auto;
     }
    }

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
      @media(max-width: 740px){
        width: 180px;
        font-size: 16px;
     }
    }
  }
}
