.root{
  li{
   display: inline-block;

   

 
    a {
      text-align: center;
      width: 45px;
      line-height: 42px;
      height: 45px;
      border: 1px solid #fe5f1e;
      border-radius: 30px;
      margin-right: 10px;
      cursor: pointer;
      display: inline-block;
      color: #fe5f1e;
      
      @media (max-width: 336px ) {
        width: 35px;
        height: 35px;
        font-size: 15px;
        line-height: 32px;
        margin-top: 30px;
      }

      &:hover {
         background-color: #fe5f1e;
         color: #fff;
      }
     
    }
 }

 :global {
   .selected {
     a{
      background-color: #fe5f1e;
      color: #fff;
     }
   }
 }
}